<template>
    <div class="main-quiz--section">
        <!-- <h4 class="main-name--area">{{ quiz.name }}</h4> -->

        <!-- HEADER AREA -->
        <div class="main-quiz-header--area">
            <i><router-link to="/">Quiz</router-link> / </i>
            <span>{{ quiz.name }}</span>
        </div>

        <!-- QUESTIONS AREA -->
        <div 
            v-for="(question, index) in shuffledData" :key="index"
            class="main-quiz-question-item--area"
        >
            <div v-show="index === questionIndex">
                <span class="main-quiz-question-page--elem">{{ index+1 }} / {{ shuffledData.length }}</span>
                <h2 class="main-quiz-question-title--elem">{{ question.title }}</h2>
                <ol class="__row_style">
                    <li 
                        v-for="(response, i) in question.responses" :key="i"
                        class="main-quiz-question-response--elem response--area"
                        @click="updateUserResponces($event, index, response.correct)"
                        :data-index="`${i+1}.`"
                    >
                        {{response.text}}
                    </li>
                </ol>
                <a 
                    class="main-quiz-question-global-btn--elem" 
                    @click="next(question.title, index)"
                >
                    next
                    <i class="las la-arrow-right"></i>
                </a>
            </div>
        </div>

        <!-- NO QUESTIONS AREA -->
        <div 
            class="main-no-questions-msg--area" 
            v-if="shuffledData.length === 0 && quizLoaded"
        >
            There is no questions added yet .. please try later!
        </div>

        <!-- SCORE AREA -->
        <div 
            v-if="toggleScoreAreas === true"
            v-show="questionIndex === shuffledData.length && showScore"
            class="main-global-score--area"
        >
            <p>Quiz finished</p>
            <div><span>Total<br />score</span><h4>{{ score }} / {{ shuffledData.length }}</h4></div>
            <a class="main-quiz-question-global-btn--elem" @click="startAgain">start again<i class="las la-undo-alt"></i></a> | 
            <a class="main-quiz-question-global-btn--elem" @click="toggleScoreAreas = !toggleScoreAreas">Check Answers<i class="las la-paste"></i></a>
        </div>

        <!-- CORRECT ANSWERS AREA -->
        <div 
            v-if="toggleScoreAreas === false"
            v-show="questionIndex === shuffledData.length && showScore"
            class="main-user-answers--area"
        >   
            <p>Quiz finished</p>

            <div>
                <div class="main-question-item-with-real-answer--elem add-bottom-border">
                    <div class="main-question-col--item">Question:</div>
                    <div class="main-answer-col--item">Rigth Answer:</div>
                </div>
                <div 
                    v-for="(answer, index) in ansersTable" :key="index"
                    class="main-question-item-with-real-answer--elem main-answers--elem"
                    :class="(answer.isCorrect) ? 'isSuccess' : 'isFail'"
                >
                    <!--  -->
                    <div class="main-question-col--item">
                        <p><i>{{index+1}}.</i> {{ answer.title }}</p>
                        <!-- <i v-if="answer.isCorrect === true" class="las la-smile"></i>
                        <i v-if="answer.isCorrect === false" class="las la-frown"></i> -->

                        <span v-if="answer.isCorrect === true" class="main-emoji--elem success">ðŸ¥³</span>
                        <span v-if="answer.isCorrect === false" class="main-emoji--elem fail">ðŸ˜Ÿ</span>
                    </div>
                    <div class="main-answer-col--item">
                        <div>
                            {{ answer.correctAnswer[0].text }} 
                            <!-- <i class="las la-check-double"></i> -->
                            <span class="main-emoji-verified--elem">âœ…</span>
                        </div>
                    </div>
                </div>
            </div>

            <a class="main-quiz-question-global-btn--elem" @click="startAgain">start again<i class="las la-undo-alt"></i></a> | 
            <a class="main-quiz-question-global-btn--elem" @click="toggleScoreAreas = !toggleScoreAreas">My Score<i class="las la-trophy"></i></a>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import axios from 'axios';

export default {
    data() {
        return {
            id: this.$route.params.id,
            quiz: [],//quiz,
            // Store current question index
            questionIndex: 0,
            // An array initialized with "false" values for each question
            // It means: "did the user answered correctly to the question n?" "no".
            userResponses: [],//Array(quiz.questions.length).fill(false),
            showScore: false,
            score: 0,
            shuffledData: [],
            ansersTable: [],
            toggleScoreAreas: true,

            quizLoaded: false
        }
    },
    created() {
        this.shuffleArrData();
    },
    // The view will trigger these methods on click
    methods: {
        async shuffleArrData(){
            try {
                const data = await axios({
                    method: "GET",
                    url: `/api/v1/quiz/${this.id}`
                });

                this.quiz = data.data.data.data;
                this.userResponses = Array(this.quiz.questions.length).fill(false);

                this.shuffledData = this.quiz.questions.map(el => {
                    el.responses.sort(() => .5 - Math.random())
                    return el;
                });

                if(this.shuffledData.length) this.showScore = true;
            } catch (error) {
                return this.$router.push('/error');
            }
            this.quizLoaded = true;
        },

        next(title, i) { 
            this.questionIndex++;
            this.removeOnFocusClass();

            let isCorrect = this.userResponses[i];

            const obj = {};
            obj.title = title;
            obj.correctAnswer = this.shuffledData[i].responses.filter(el => el.correct === true);
            obj.isCorrect = isCorrect;
            let req = obj;

            this.ansersTable.push(req);
        },
        prev() { this.questionIndex--; },
        startAgain() {
            this.questionIndex = 0;
            this.score = 0;
            this.ansersTable = [];
            this.toggleScoreAreas = true;
            this.userResponses = Array(this.quiz.questions.length).fill(false);
            this.shuffledData = this.quiz.questions.map(el => {
                el.responses.sort(() => .5 - Math.random())
                return el;
            });
        },
        
        updateUserResponces(e, i, val) {
            this.removeOnFocusClass();
            e.target.classList.add("isClicked");

            this.userResponses[i] = val;
            this.score = this.userResponses.filter(val => val).length
        },
        removeOnFocusClass(){
            const elems = document.querySelectorAll(".response--area");
            [].forEach.call(elems, function(el) {
                el.classList.remove("isClicked");
            });
        },
    }
}
</script>

